import React from 'react';

interface ScrollIndicatorProps {
  /** Función onClick opcional */
  onClick?: () => void;
  /** Clases CSS adicionales */
  className?: string;
}

export default function ScrollIndicator({ onClick, className = '' }: ScrollIndicatorProps) {
  return (
    <div 
      className={`absolute bottom-12 left-1/2 transform -translate-x-1/2 flex flex-col items-center opacity-60 hover:opacity-100 transition-opacity duration-500 cursor-pointer ${className}`}
      onClick={onClick}
    >
      {/* Línea decorativa superior */}
      <div className="w-px h-12 bg-gradient-to-b from-transparent via-amber-300/40 to-transparent mb-3"></div>
      
      {/* Texto sutil */}
      <div 
        className="text-amber-200/70 text-xs font-light tracking-[0.2em] mb-4" 
        style={{ fontFamily: 'Montserrat, sans-serif' }}
      >
        SCROLL
      </div>
      
      {/* Indicador de mouse */}
      <div className="relative">
        <div className="w-5 h-8 border border-amber-300/40 rounded-full">
          <div className="absolute top-2 left-1/2 transform -translate-x-1/2 w-px h-2 bg-amber-300/60 rounded-full animate-pulse"></div>
        </div>
      </div>
    </div>
  );
}